@import 'utils/variables', 'utils/mixins';

.wrapper {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.dataHeader {
  margin-top: 30px;

  font-size: 16px;
  font-weight: 700;
  line-height: 28px;
  color: $base-black;

  &:last-child {
    margin-top: 44px;
  }

  @include respond-to('medium') {
    margin-top: 20px;

    font-size: 13px;
    line-height: 15px;

    &:last-child {
      margin-top: 30px;
    }
  }

  @include darkTheme {
    color: $dark-white;
  }
}

.container {
  margin-top: 15px;
  margin-bottom: 15px;
  width: 100%;

  display: flex;
  flex-direction: row;

  align-items: center;

  @include respond-to('medium') {
    margin-top: 15px;
  }
}

.containerData {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;

  @include respond-to('medium') {
    margin-top: 17px;
  }
}

.containerSmall {
  width: 180px;
  height: 67px;

  &:nth-child(3) {
    padding-top: 19px;
    margin-left: -1px;
  }

  @include respond-to('medium') {
    width: 130px;
    height: 44px;
  }
}

.containerMinor {
  width: 200px;
  height: 67px;
  margin-right: 10px;

  &__error {
    input {
      border-color: #ff6161 !important;
    }
  }

  @include respond-to('medium') {
    width: 185px;
    height: 44px;
  }
}

.containerMedium {
  width: 500px;
  height: 67px;

  @include respond-to('medium') {
    width: 272.5px;
    height: 44px;
  }
}

.containerLarge {
  width: 473px;
  height: 67px;
  margin-right: 10px;

  &__error {
    input {
      border-color: #ff6161 !important;
    }
  }

  @include respond-to('medium') {
    width: 360px;
    height: 44px;
  }
}

.containerHuge {
  width: 100%;
  height: 67px;

  @include respond-to('medium') {
    height: 44px;
  }
}

.checkboxWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
}

.checkbox {
  display: flex;
  align-items: center;

  position: relative;
  bottom: -15px;
}

.actions {
  margin-top: 30px;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.action {
  width: 175px;
  height: 45px;
  margin-left: 10px;

  @include respond-to('medium') {
    width: 160px;
    height: 30px;
  }
}

.action span {
  font-size: 15px;
  line-height: 24px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.action button {
  width: 100%;
  height: 100%;
}

.dropdown {
  width: 380px;
  height: 67px;

  margin-right: 10px;

  @include respond-to('medium') {
    width: 272.5px;
    height: 44px;
  }
}

.dropdownContainer {
  margin-top: 8px;

  width: 100%;
  min-height: 45px;
  height: 45px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 4px;

  border: 1px solid $base-dark-gray;
  box-sizing: border-box;
  border-radius: 6px;

  &__error {
    border-color: #ff6161 !important;
  }

  @include respond-to('large') {
    min-height: 40px;
    height: 40px;
    border-radius: 6px;
  }

  @include respond-to('medium') {
    min-height: 25px;
    height: 25px;
    border-radius: 4px;
  }
}

.dropdownLabel {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $base-black;

  @include respond-to('medium') {
    font-size: 10px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $dark-white;
  }
}

.dropdownContainer {
  padding: 12px;

  @include darkTheme {
    background: $light-black;
  }
}

.dropdownValue {
  font-size: 14px;
  line-height: 20px;
  font-weight: 300;
  color: $light-grey;

  &Disabled {
    color: #adadad;
  }

  @include respond-to('medium') {
    font-size: 10.5px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $dark-white;
    &Disabled {
      color: $grey;
    }
  }
}

.checkboxContainer {
  position: relative;
  top: -4px;

  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.checkbox {
  width: 17px;
  height: 17px;

  padding: 2px;

  border-radius: 2px;
  box-sizing: border-box;
  border: 0.5px solid $blue;

  cursor: pointer;

  @include darkTheme {
    border-color: $moderate-blue;
  }
}

.active {
  width: 11px;
  height: 11px;

  border-radius: 2px;
  box-sizing: border-box;
  background-color: $blue;

  @include darkTheme {
    background: $moderate-blue;
  }
}

.checkboxLabel {
  margin-left: 10px;

  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #2a2f42;

  @include darkTheme {
    color: $white;
  }
}

.radio {
  width: 73%;
  display: flex;
  flex-direction: row;

  align-items: center;
  font-size: 15px;

  cursor: pointer;

  @include darkTheme {
    color: $dark-white;
  }

  div {
    display: flex;
    align-items: center;

    margin-right: 10px;
  }

  span {
    margin: 0 5px;
    color: #45474b;

    @include darkTheme {
      color: $dark-white;
    }
  }

  svg {
    width: 18px;
    height: 18px;
    color: $dark-blue;

    @include darkTheme {
      color: $moderate-blue;

      circle:nth-child(2) {
        fill: $black-blue;
      }
    }
  }

  @include respond-to('large') {
    font-size: 14px;
  }

  @include respond-to('medium') {
    font-size: 11px;
  }
}

:global(.react-datepicker-component.is-disabled) {
  opacity: 1 !important;
}

:global(.react-datepicker-component) {
  margin-top: 12px;

  @include respond-to('large') {
    margin-top: 10px;
  }
}

:global(.react-datepicker-component .react-datepicker-input input) {
  font-size: 14px !important;
  padding-left: 0 !important;

  @include respond-to('medium') {
    font-size: 11px !important;
    padding-left: 0 !important;
  }
}

.calendar_icon {
  width: 20px;
  height: 20px;
  cursor: pointer;

  position: absolute;
  margin-top: 42px;
  margin-left: 168px;

  @include respond-to('large') {
    margin-top: 38px;
  }
}

:global(.react-datepicker-component).dataInput {
  :global(.react-datepicker-input),
  :global(.react-datepicker-input.has-value),
  :global(.react-datepicker-input.is-open) {
    border-radius: 4px;
    font-weight: 400 !important;
    background: transparent;
    border: 1px solid $base-dark-gray;
    width: 200px;
    height: 46px;
    padding-left: 10px;
    cursor: pointer;

    &:hover {
      background: transparent;
      border: 1px solid #d2e8ff;
    }

    input {
      font-weight: 300;

      &::placeholder {
        font-weight: 100;
      }
    }

    @include respond-to('large') {
      height: 40px;
      border-radius: 4px;
    }

    @include respond-to('medium') {
      font-size: 10.5px;
      line-height: 12px;
      height: 25px;
      border-radius: 4px;

      padding: 12px;
    }

    @include darkTheme {
      color: $dark-white;

      input {
        color: $dark-white;
      }
    }
  }
}

:global(.react-datepicker-component .react-datepicker-input input::placeholder) {
  font-weight: 400 !important;
}

:global(.react-datepicker-component .react-datepicker-input input) {
  font-weight: 400 !important;
  cursor: pointer;
}

:global(.react-datepicker .react-datepicker-container .react-datepicker-top) {
  @include darkTheme {
    background: $black-grey !important;
    border-color: $dark-white !important;
    color: $dark-white;
  }
}

:global(.react-datepicker-container) {
  margin-left: -55px;
}

:global(
    .react-datepicker
      .react-datepicker-container
      .react-datepicker-body
      .react-datepicker-row
      .react-datepicker-picker
  ) {
  @include darkTheme {
    background: $base-black !important;
    color: $dark-white !important;
  }
}

:global(.react-datepicker-input.has-value) {
  @include darkTheme {
    color: $dark-white !important;

    input {
      color: $dark-white !important;
    }
  }
}

:global(.button-wrapper) {
  width: 0;
}

.dataInputError :global(.react-datepicker-input) {
  background: none !important;
  border: 1px solid #ff6161 !important;
  border-radius: 8px !important;
  margin-top: 8px;
  font-weight: 300 !important;

  width: 280px;
  height: 42px !important;

  cursor: pointer;

  input {
    font-weight: 300 !important;
  }

  @include respond-to('medium') {
    width: 185px;
    height: 25px !important;

    border-radius: 4px !important;
  }
}

.dataSpan {
  font-size: 14px;
  line-height: 18px;
  color: $base-black;

  @include respond-to('medium') {
    font-size: 10px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $dark-white;
  }
}

.question_icon {
  margin-left: 10px;
  width: 15px;
  height: 15px;

  cursor: pointer;

  @include respond-to('medium') {
    margin-left: 3px;

    width: 10px;
    height: 10px;
  }

  @include darkTheme {
    path {
      fill: $middle-blue;
    }
  }
}

.spanTooltip {
  font-size: 17px;

  @include respond-to('large') {
    font-size: 12px;
  }

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.redStar {
  color: #ff6161;
}

.row {
  display: flex;
  align-items: center;
}

.separateLine {
  border-bottom: 1px solid $base-gray;
  margin: 25px -15px 0px -15px;
  width: 111%;
}

.cancelButton {
  cursor: pointer;

  @include darkTheme {
    color: $dark-white;
  }
}

.listWrapper {
  margin-top: -25px;

  width: 98%;
  position: absolute;
  top: 100%;
  z-index: 2;
  box-shadow: 0px 16px 37px 0px rgba(0, 0, 0, 0.16);

  background-color: $white;
  border-radius: 4px;

  @include darkTheme {
    border-color: $grey;
  }
}

.labelWithIcon {
  display: flex;
  align-items: center;

  margin-bottom: 2px;
}

.labelDropdownWithIcon {
  display: flex;
  align-items: center;

  margin-bottom: 10px;
}

button.button {
  height: 40px;
  width: auto;

  font-size: 16px;
  line-height: 24px;

  padding: 0 24px;

  color: $white;

  @include respond-to('large') {
    height: 40px;
    width: auto;

    font-size: 16px;
    line-height: 24px;

    padding: 0 24px;
  }

  @include respond-to('medium') {
    height: 40px;
    width: auto;

    font-size: 16px;
    line-height: 24px;

    padding: 0 24px;
  }

  &.apply {
    background-color: $dark-blue;
    color: $white;

    @include darkTheme {
      background-color: $moderate-blue;
      color: $jet;
      border: $moderate-blue;
    }

    &:disabled {
      background-color: $blue-button;
      color: $white;
      border: $blue-button;

      @include darkTheme {
        background-color: $grey;
        color: $jet;
        border: $grey;
      }
    }
  }
}