@import 'utils/variables', 'utils/mixins';

.wrapper {
  margin-top: 30px;
  margin-bottom: 50px;

  @include respond-to('medium') {
    margin-top: 20px;
    margin-bottom: 50px;
  }
}

.container {
  margin-top: 18px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  @include respond-to('medium') {
    margin-top: 25px;
  }
}

.containerMedium {
  width: 440px;
  height: 67px;

  &__error {
    input {
      border-color: #FF6161 !important;
    }
  }

  @include respond-to('medium') {
    width: 270px;
    height: 44px;
  }
}

.dataLabel {
  font-size: 14px;
  line-height: 18px;
  color: $base-black;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.commentsContainer {
  width: 100%;
  height: 90px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;

  font-size: 16px;
  line-height: 22px;

  @include respond-to('medium') {
    width: 650px;
    height: 59px;

    font-size: 16px;
    line-height: 22px;
  }
}

.commentsContainer textarea {
  margin-top: 8px;
  padding: 12px 20px;

  resize: none;
  width: 100%;
  height: 100%;

  outline: none;

  border: 1px solid $base-dark-gray;
  box-sizing: border-box;
  border-radius: 4px;

  font-family: $roboto;
  font-style: normal;
  font-size: 16px;
  line-height: 20px;

  &:disabled {
    background-color: $white;
  }

  @include respond-to('medium') {
    margin-top: 8px;
    padding: 12px;

    font-size: 10.5px;
    line-height: 12px;
  }

  @include darkTheme {
    background: $black-blue;
    color: $white;
  }
}

.addFile {
  margin-top: 18px;
  width: 250px;
  height: 45px;

  @include respond-to('medium') {
    margin-top: 18px;
    width: 200px;
    height: 30px;
  }
}

.addFile span {
  font-size: 14px;
  line-height: 24px;

  @include respond-to('medium') {
    font-size: 12px;
    line-height: 14px;
  }
}

.fileList {
  margin-bottom: 14px;

  display: flex;
  flex-flow: row wrap;
}

.fileItem {
  margin-top: 14px;
  margin-right: 11px;
  height: 45px;

  padding: 18px 20px;

  display: flex;
  flex-direction: row;
  align-items: center;

  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
  cursor: pointer;

  border-radius: 8px;
  background: $white;
  border: 1px solid $light-blue;
  box-sizing: border-box;
  box-shadow: 1px 1px 4px #A1C0E0;

  @include respond-to('medium') {
    margin-right: 10px;
    height: 30px;

    padding: 9px 10px;

    font-size: 10.5px;
    line-height: 12px;

    border: 0.5px solid $light-blue;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.15);
  }

  @include darkTheme {
    background: $black-blue;
    color: $white;
  }
}

.fileItemRemove {
  margin-left: 22px;
  width: 9px;
  height: 9px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 8px;
    height: 8px;
  }
}

.actions {
  margin-top: 30px;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.action {
  width: 175px;
  height: 45px;
  margin-left: 10px;

  @include respond-to('medium') {
    width: 160px;
    height: 30px;
  }
}

.action span {
  font-size: 14px;
  line-height: 24px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.action button {
  width: 100%;
  height: 100%;
}

.dropdownBig {
  width: 100%;
  height: 67px;

  font-weight: 300;

  flex-direction: column;
  align-items: flex-start;

  @include respond-to('medium') {
    height: 44px;
  }
}

.dropdownContainer {
  padding: 12px !important;

  display: flex;
  align-items: center;
}

.dropdownMedium {
  width: 440px;
  height: 67px;

  font-weight: 300;

  flex-direction: column;
  align-items: flex-start;

  @include respond-to('medium') {
    width: 275.5px;
    height: 45px;
  }
}

.dropdownContainer {
  margin-top: 8px;
  width: 100%;
  min-height: 45px;
  height: 45px;

  padding: 8px;

  display: flex;
  justify-content: space-between;

  border: 1px solid $base-dark-gray;
  box-sizing: border-box;
  border-radius: 4px;

  &__error {
      border-color: #FF6161 !important;
  }

  @include respond-to('large') {
    min-height: 40px;
    height: 40px;
    border-radius: 6px;
  }

  @include respond-to('medium') {
    min-height: 26px;
    height: 26px;
    border-radius: 4px;
  }
}

.dropdownLabel {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $base-black;

  @include respond-to('medium') {
    font-size: 10px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.dropdownValue {
  font-size: 16px;
  line-height: 20px;
  color: rgb(84, 84, 84);

  @include respond-to('medium') {
    font-size: 12px;
    line-height: 14px;
  }

  @include darkTheme {
    color: $white;
  }
}

.fileContainer {
  margin-top: 20px;
  margin-bottom: 14px;
}

.container_validation {
  font-weight: 100;
  color: #FF6161;
  font-size: 20px;
  padding-top: 10px;

  display: flex;
  flex-direction: row;
  align-items: flex-start;

  @include respond-to('large') {
    font-size: 12px;
  }
}

.error_icon {
  margin-right: 10px;
  margin-top: 3px;

  min-width: 14px;
  min-height: 14px;
  max-height: 14px;
  max-width: 14px;

  @include respond-to('large') {
    margin-top: -1px;
    width: 10px;
  }
}

.dataHeader {
  margin-top: 30px;

  font-size: 15px;
  font-weight: 700;
  line-height: 28px;
  color: $base-black;

  &:last-child {
    margin-top: 44px;
  }

  @include respond-to('medium') {
    margin-top: 20px;

    font-size: 13px;
    line-height: 15px;

    &:last-child {
      margin-top: 30px;
    }
  }

  @include darkTheme {
    color: $grey;
  }
}

textarea:not(:disabled):focus, textarea:not(:disabled):hover, textarea:not(:disabled):active {
  border-color: $base-dark-gray !important;
}