@import 'utils/variables', 'utils/mixins';

.wrapper {
  width: 940px;
  height: 80vh;
  position: relative;

  display: flex;
  flex-direction: column;

  background-color: $white;
  border-radius: 8px;

  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.15);

  font-family: $roboto;

  z-index: 2;
  overflow: auto;
  overflow-x: hidden;

  padding: 20px 0;

  @include respond-to('large') {
    width: 940px;
  }

  @include respond-to('medium') {
    width: 650px;
  }

  @include darkTheme {
    background: $light-black;
  }

  @include scrollbar;
}

.loader {
  margin-top: 50px;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.loaderContainer {
  position: absolute;
  top: 46%;
  left: 48%;
  z-index: 2;
}

.background {
  z-index: 1;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.25);
  height: 100%;
  width: 100%;
}

input:disabled {
  background-color: $white;
}

.header {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  padding: 0 20px;

  @include respond-to('small') {
    padding: 20px 20px 10px 20px;
  }
}

.headerLabel {
  font-size: $header;
  line-height: 24px;
  color: $base-black;
  font-weight: 700;

  @include darkTheme {
    color: $dark-white;
  }
}

.close {
  width: 13px;
  height: 13px;

  margin-bottom: -2px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 14px;
    height: 14px;
  }
}

.spacer {
  min-height: 30px;
  width: 100%;

  @include respond-to('medium') {
    min-height: 15px;
  }
}

.tabList {
  list-style: none;

  margin-block-start: 0;
  margin-block-end: 0;
  padding-inline-start: 0;

  width: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;

  border-bottom: 1px solid $base-gray;

  overflow: hidden;

  @include darkTheme {
    border-color: $light-grey;
  }
}

.tabItem {
  height: 45px;
  width: 28%;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: $white;
  border: none;
  border-radius: 8px;
  box-sizing: border-box;

  cursor: pointer;

  font-weight: 300;
  font-size: 15px;
  line-height: 18px;
  color: $base-middle-gray;
  text-align: center;

  position: relative;
  bottom: -8px;

  &[aria-selected='true'] {
    bottom: 0;

    background-color: $white;
    border: none;
    border-bottom: 2px solid $dark-blue;
    border-radius: 0;

    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    color: $dark-blue;

    @include respond-to('medium') {
      bottom: -1px;
      font-size: 12px;
      line-height: 13px;
    }

    @include darkTheme {
      border-color: $grey;
      background: $light-black;
      color: $dark-white;
    }
  }

  @include respond-to('medium') {
    height: 40px;
    width: 142.5px;

    font-size: 11px;
    line-height: 13px;

    position: relative;
    bottom: -6px;
  }

  @include darkTheme {
    border-color: $grey;
    background: $moderate-blue;
    color: $jet;
  }
}

.tabPanel {
  padding: 0 20px;
}

@media only screen and (max-height: 600px) {
  .detailed-node {
    height: 80vh;
  }
}

.headerFixed {
  top: 0;
  background-color: white;
  z-index: 2;

  @include darkTheme {
    background: $light-black;
  }
}

.tabs {
  z-index: 1;
}

.separateLine {
  border-bottom: 1px solid $light-grey;
  margin: 15px -15px 25px -15px;
  width: 111%;
}
