@import 'utils/variables', 'utils/mixins';

.fileContainer {
  margin-top: 20px;
  margin-bottom: 14px;
}

.actions {
  margin-top: 10px;

  display: flex;
  flex-direction: row;
  align-items: center;

  height: 100px;
  width: 100%;
  border: 2px dashed $base-dark-gray;
  padding: 20px;

  border-radius: 4px;
}

.action {
  width: 210px;
  height: 45px;

  &:first-child {
    margin-right: 25px;
  }

  @include respond-to('medium') {
    &:first-child {
      margin-right: 20px;
    }
    width: 200px;
    height: 30px;
  }
}


.action span {
  font-size: 16px;
  line-height: 24px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.action button {
  width: 100%;
  height: 100%;
}

.fileList {
  display: flex;
  flex-flow: column;
}

.fileItem {
  margin-top: 5px;
  height: 45px;

  padding: 18px 20px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
  
  border-radius: 4px;
  background: #EFEFEF;

  box-sizing: border-box;

  cursor: pointer;

  @include respond-to('medium') {
      height: 30px;
    
      padding: 9px 10px;
    
      font-size: 10.5px;
      line-height: 12px;
  }

  @include darkTheme {
    color: $white;
    background: $black-blue;
  }
}

.fileItemRemove {
  margin-left: 22px;
  width: 11px;
  height: 11px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 8px;
    height: 8px;
  }

  @include darkTheme {
    path {
      stroke: $moderate-blue;
    }
  }
}

.data_label {
  font-size: 14px;
  line-height: 18px;
  color: $base-black;

  @include respond-to('medium') {
    font-size: 11px;
    line-height: 13px;
  }

  @include darkTheme {
    color: $moderate-blue;
  }
}

.addFile {
  margin-top: 18px;
  width: 250px;
  height: 45px;

  @include respond-to('medium') {
    margin-top: 18px;
    width: 200px;
    height: 30px;
  }
}

.addFile span {
  font-size: 14px;
  line-height: 24px;

  @include respond-to('medium') {
    font-size: 12px;
    line-height: 14px;
  }
}

.title {
  font-size: 14px;
  color: $base-black;
  font-weight: 700;

  margin-bottom: 2px;
}

.comment {
  font-size: 14px;
  color: $base-middle-gray;
}

.filesTooltip {
  display: flex;
  flex-direction: column;
}

.fileIcon {
  width: 15px;
  height: 15px;
  margin-right: 20px;
}

.fileLabel {
  display: flex;
  align-items: center;
}