@import '../../styles/utils/variables', '../../styles/utils/mixins';

.label {
  font-size: 16px;
  line-height: 18px;
  color: $base-black;
  white-space: nowrap;

  @include respond-to('medium') {
    font-size: 10px;
    line-height: 12px;
  }

  @include darkTheme {
    color: $dark-white;
  }
}

.custom_input {
  margin-top: 8px;
  padding: 12px 10px;
  height: 45px;
  width: 100%;

  outline: none;

  border: 1px solid $base-dark-gray;
  box-sizing: border-box;
  border-radius: 4px;

  font-size: 14px;
  line-height: 20px;

  &::selection {
    background-color: #d2e8ff;
  }

  @include respond-to('large') {
    height: 40px;
    border-radius: 4px;
  }

  @include respond-to('medium') {
    font-size: 10.5px;
    line-height: 12px;
    height: 25px;
    border-radius: 4px;

    padding: 12px;
  }

  @include darkTheme {
    color: $dark-white;
    background: $light-black;
  }
}

.disabled {
  cursor: not-allowed;

  @include darkTheme {
    background: $black-grey-light;
    color: $grey;
  }
}

.withIcon {
  background: url(./assets/arrowDown.svg);
  background-position: 8px 12px;
  background-repeat: no-repeat;
  background-size: 5%;

  @include respond-to('medium') {
    background-position: 6px 8px;
    background-size: 4%;
  }
}

.onCopy {
  cursor: text;

  @include darkTheme {
    background: $light-black;
    color: $dark-white;
  }
}

.spanTooltip {
  font-size: 17px;

  @include respond-to('large') {
    font-size: 12px;
  }

  @include respond-to('medium') {
    font-size: 11px;
  }
}
